.userList{
    flex: 4;
}
.userListUser{
    display: flex;
    align-items: center;
}
.userListImg{
    width: 32px;
    height: 32px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 10px;
}
.userListEdit{
border: none;
border-radius: 10px;
padding: 5px 10px;
margin-right: 10px;
background-color: #3bb077;
color: #ffff;
cursor: pointer;
}
.userListDelete{
color: red;
cursor: pointer;
}